body{
  background-color: #edf6f9;
}

.Slay{
  background-color: #83c5be;
  border-radius: 100px;
  margin: 0 auto;
  transition: 0.3s;
  cursor: pointer;
  text-align: center;
  height: 70px;
  width: 120px;
  padding: 5px;
}

.Slay h1 {
  font-size: 37px;
  color: #006d77;
  position: relative;
  top: -14px;
  user-select: none;
}

#SlayPlacement{
  position: absolute;
  bottom: 50px;
  left: 50px;
}

.Neigh{
  background-color: #e29578;
  border-radius: 100px;
  margin: 0 auto;
  transition: 0.3s;
  cursor: pointer;
  text-align: center;
  height: 70px;
  width: 120px;
  padding: 5px;
}

.Neigh h1 {
  font-size: 37px;
  color: #ffddd2;
  position: relative;
  top: -14px;
  user-select: none;
}

#NeighPlacement{
  position: absolute;
  bottom: 50px;
  right: 50px;
}

#ImageDisplay{
  z-index: -1;
}

#ImageDisplay img{
  width: 120%;
  position: relative;
  left: -10%;
}